const ADD_TASK = 'ADD-TASK';
const UPDATE_NEW_TASK_TEXT = 'UPDATE-NEW-TASK-TEXT';

let initialState = {
    arrListData: [
        {id: 0, description: '–°—Ö–æ–¥–∏—Ç—å –≤ –º–∞–≥–∞–∑–∏–Ω', completed: false},
        {id: 1, description: '–ü–æ–º—ã—Ç—å –∫–æ—Ç–∞', completed: false},
        {id: 2, description: '–∑–∞–∫–æ–Ω—á–∏—Ç—å —Ç–µ—Å—Ç–æ–≤–æ–µ –∑–∞–¥–∞–Ω–∏–µ', completed: false},
        {id: 3, description: '–ü–æ–¥–≥–æ—Ç–æ–≤–∏—Ç—å –ø—Ä–µ–∑–µ–Ω—Ç–∞—Ü–∏—é', completed: false},
        {id: 4, description: '–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å 100 –ª–µ–∫—Ü–∏–π –ø–æ react', completed: false}
    ],
    newTaskText: ''
};

const tasksReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_TASK: {
            let newTask = {
                id: state.arrListData.length,
                description: state.newTaskText,
                completed: false
            };
            return {
                ...state,
                arrListData: [...state.arrListData, newTask],
                newTaskText: ''
            };
        }
        case UPDATE_NEW_TASK_TEXT: {
            return {
                ...state,
                newTaskText: action.updateTask
            };
        }
        default:
            return state;
    }
}

export const addTaskActionCreator = () => {
    return {
        type: ADD_TASK
    }
}

export const updateNewTaskTextActionCreator = (taskText) => {
    return {
        type: UPDATE_NEW_TASK_TEXT,
        updateTask: taskText
    }
}

export default tasksReducer;